import '@/plugins/axios.js';
import axios from 'axios';

var cache_data = {};
var cache_time = {};
var cache_resv = {};
var cache_rejc = {};

function buildRequest(method, url, data) {
  return new Promise((resolve, reject) => {
    let config = {
      method: method,
      url: url
    }

    if (method === 'POST' || method === 'PUT')
      config.data = data;
    else if (data !== undefined) {
      config.params = {};
      Object.keys(data).forEach(k => {
        if (data[k] !== null && data[k] !== undefined)
          config.params[k] = data[k];
      });
    }

    axios(config).then(resp => {
      if (resp.data.data !== undefined)
        resolve(resp.data.data);
      else
        resolve(null);
    }).catch(err => {
      reject(err);
    });
  });
}

function buildRequestWithCache(method, url, data) {
  return new Promise((resolve, reject) => {
    if (cache_time[url] === undefined || cache_time[url] < Date.now() - 1000 * 60 * 5) {
      cache_time[url] = Date.now();
      cache_resv[url] = [resolve];
      cache_rejc[url] = [reject]
      buildRequest(method, url, data).then(data => {
        cache_data[url] = data;
        cache_resv[url].forEach(resolve => resolve(data));
        cache_resv[url] = [];
        cache_rejc[url] = [];
      }).catch(err => {
        cache_rejc[url].forEach(reject => reject(err));
        cache_resv[url] = [];
        cache_rejc[url] = [];
      });
    } else if (cache_data[url] === undefined) {
      cache_resv[url].push(resolve);
      cache_rejc[url].push(reject);
    } else {
      resolve(cache_data[url]);
    }
  });
}

export default {
  login(username, password) {
    return buildRequest('POST', '/auth/login', {
      username: username,
      password: password
    });
  },

  getRoomList(params) {
    return buildRequest('GET', `/rooms`, params);
  },
  createRoom(data) {
    return buildRequest('POST', '/room', data);
  },
  updateRoom(room_id, data) {
    return buildRequest('PUT', `/room/${room_id}`, data);
  },
  deleteRoom(room_id) {
    return buildRequest('DELETE', `/room/${room_id}`);
  },
  getRoomMedia(room_id) {
    return buildRequest('GET', `/room/${room_id}/media`);
  },
  getRoomDetail(room_id) {
    return buildRequest('GET', `/room/${room_id}`);
  },
  getRoomRecordList(room_id) {
    return buildRequest('GET', `/room/${room_id}/records`);
  },
  getRoomTree(excludeOther=0) {
    return buildRequest('GET', `/rooms/tree?exclude_other=${excludeOther}`);
  },
  exportRoomDocument() {
    return buildRequest('GET', `/rooms/document/export`);
  },
  exportRoomHistory() {
    return buildRequest('GET', `/rooms/history/export`);
  },

  getCampusList() {
    return buildRequestWithCache('GET', '/campus');
  },
  getBuildingsInCampus(campus_id) {
    return buildRequest('GET', `/campus/${campus_id}/buildings`);
  },
  getRoomsInBuilding(building_id) {
    return buildRequest('GET', `/building/${building_id}/rooms`);
  },

  getRoomTypes() {
    return buildRequestWithCache('GET', '/types/room');
  },
  getMissionTypes() {
    return buildRequestWithCache('GET', '/types/mission');
  },

  uploadImage(file) {
    let formData = new FormData();
    formData.append("file", file);
    return new Promise((resolve, reject) => {
      axios({
        method: 'POST',
        url: '/upload',
        data: formData,
        headers: {'Content-Type': 'multipart/form-data'}
      }).then(resp => {
        if (resp.data.data !== undefined)
          resolve(resp.data.data);
        else
          resolve(null);
      }).catch(err => {
        reject(err);
      });
    });
  },

  getUserList() {
    return buildRequestWithCache('GET', '/users');
  },
  getUserDetail(user_id) {
    return buildRequest('GET', `/user/${user_id}`);
  },
  createUser(data) {
    return buildRequest('POST', '/user', data);
  },
  updateUser(user_id, data) {
    return buildRequest('PUT', `/user/${user_id}`, data);
  },
  deleteUser(user_id) {
    return buildRequest('DELETE', `/user/${user_id}`);
  },
  
  createMission(data) {
    return buildRequest('POST', '/mission', data);
  },
  deleteMission(mission_id) {
    return buildRequest('DELETE', `/mission/${mission_id}`);
  },
  deleteMultiMissions(mission_ids) {
    return buildRequest('DELETE', `/missions`, {mission_ids: mission_ids.join(',')});
  },
  restartMission(mission_id) {
    return buildRequest('GET', `/mission/${mission_id}/restart`);
  },
  restartMultiMissions(mission_ids) {
    return buildRequest('GET', `/missions/restart`, {mission_ids: mission_ids.join(',')});
  },
  cancelMission(mission_id, description="") {
    return buildRequest('GET', `/mission/${mission_id}/cancel`, {description: description});
  },
  cancelMultiMissions(mission_ids, description="") {
    return buildRequest('GET', `/missions/cancel`, {
      mission_ids: mission_ids.join(','),
      description: description});
  },
  getMissionList(params) {
    return buildRequest('GET', `/missions`, params);
  },
  getMissionDetail(mission_id) {
    return buildRequest('GET', `/mission/${mission_id}`);
  },
  submitMission(mission_id, presave, data) {
    return buildRequest('POST', `/mission/${mission_id}/submit?presave=${presave}`, data);
  },
  getMissionRecord(mission_id) {
    return buildRequest('GET', `/mission/${mission_id}/record`);
  },
  exportMissions(from_date, end_date) {
    return buildRequest('GET', `/missions/export`, {
      from_date,
      end_date
    });
  },

  getProblemList(solved, page, per_page=30) {
    return buildRequest('GET', `/problems`, {
      solved,
      page,
      per_page
    });
  },
  solveProblem(problem_id) {
    return buildRequest('GET', `/problem/${problem_id}/solve`);
  },
  reopenProblem(problem_id) {
    return buildRequest('GET', `/problem/${problem_id}/reopen`);
  },

  getDeviceInRoom(room_id, status, page, per_page=30) {
    return buildRequest('GET', `/room/${room_id}/devices`, {
      status,
      page,
      per_page
    });
  },
  createDevice(room_id, data) {
    return buildRequest('POST', `/room/${room_id}/device`, data);
  },
  getDeviceDetail(device_id) {
    return buildRequest('GET', `/device/${device_id}`);
  },
  updateDevice(device_id, data) {
    return buildRequest('PUT', `/device/${device_id}`, data);
  },
  deleteDevice(device_id) {
    return buildRequest('DELETE', `/device/${device_id}`);
  },
  downDevice(device_id, data) {
    return buildRequest('POST', `/device/${device_id}/down`, data);
  },
}