<template>
	<div>
		<el-container>
			<el-header>
				<el-menu class="menu" mode="horizontal" :router="true" :default-active="defaultActive">
					<h4>弱电间管理系统</h4>
					<div class="header-right">
						<el-submenu index="#">
							<template slot="title"><i
									class="el-icon-s-custom"></i>{{ realname }}&nbsp;&nbsp;&nbsp;&nbsp;</template>
							<el-menu-item index="#logout" @click="logout">登出</el-menu-item>
						</el-submenu>
					</div>
				</el-menu>
			</el-header>
			<el-main :style="defaultHeight">
				<div>
					<div id="mission-list">

						<el-tabs v-model="activeTab" type="card">
							<el-tab-pane label="未完成任务" name="current"></el-tab-pane>
							<el-tab-pane label="暂存任务" name="presave"></el-tab-pane>
							<el-tab-pane label="历史任务" name="history"></el-tab-pane>
							<mission-current ref="current" v-if="activeTab === 'current'" />
							<mission-presave ref="presave" v-if="activeTab === 'presave'" />
							<mission-history ref="history" v-if="activeTab === 'history'" />
						</el-tabs>
					</div>
				</div>
			</el-main>
			<el-footer>
				<el-row>
					<el-col :span="8">
						<div>
							<el-button icon="el-icon-menu" circle @click="display"></el-button>
							<div>
								<span style="font-size: 10px;">任务中心</span>
							</div>
						</div>
					</el-col>
					<el-col :span="8">
						<div>
							<el-button icon="el-icon-search" circle @click="searchPosition"></el-button>
							<div>
								<span style="font-size: 10px;">查看地址</span>
							</div>
						</div>
					</el-col>
					<el-col :span="8">
						<div>
							<el-button icon="el-icon-user" circle @click="personalCenter"></el-button>
							<div>
								<span style="font-size: 10px;">个人中心</span>
							</div>
						</div>
					</el-col>
				</el-row>
			</el-footer>
		</el-container>
	</div>
</template>

<script>
	import MissionCurrent from '@/components/MissionCurrent.vue';
	import MissionPresave from '@/components/MissionPresave.vue';
	import MissionHistory from '@/components/MissionHistory.vue';

	export default {
		components: {
			MissionCurrent,
			MissionPresave,
			MissionHistory,
		},
		methods: {
			missionCreated() {
				this.activeTab = 'current';
				this.$refs['current'].currentPage = 1;
				this.$refs['current'].getData();
			},
			exportMissions() {
				this.exportLoading = true;
				this.API.exportMissions(
					parseInt(this.exportDate[0].getTime() / 1000),
					parseInt(this.exportDate[1].getTime() / 1000) + 24 * 60 * 60
				).then(data => {
					this.exportNum = data['count'];
					this.exportUrl = this.STATIC_URL + data['filename'];
				}).finally(() => {
					this.exportLoading = false;
				});
			},
			logout() {
				this.$notify({
					title: "登出成功",
					message: `您已登出，请重新登录`,
					type: "success",
					duration: 1000,
					onClose: () => {
						localStorage.clear();
						this.$router.push('/');
					},
				});
			},
			getHeight() {
				this.defaultHeight.height = window.innerHeight - 120 + "px";
			},
			display() {
				this.$router.push("/display")
			},
			searchPosition() {
				this.$router.push("/searchPosition")
			},
			personalCenter() {
				this.$router.push(`/personalCenter/${localStorage.user_id}`)
			},
		},
		created() {
			window.addEventListener("resize", this.getHeight());
			this.getHeight();
		},
		data() {
			return {
				defaultHeight: {
					height: ""
				},
				user_id: localStorage.getItem('user_id'),
				realname: localStorage.getItem('realname'),
				activeTab: 'current',
				exportVisible: false,
				exportLoading: false,
				exportDate: [new Date(new Date().getTime() - 30 * 24 * 60 * 60 * 1000), new Date()],
				exportUrl: '',
				exportNum: 0
			}
		}
	}
</script>

<style lang="scss" scoped>
	.menu {
		display: flex;
		justify-content: flex-start;
		width: 100%;
	}

	.header-right {
		margin-left: auto;
	}

	.el-footer {
		background-color: #f3f8fa;
	}

	#mission-list {
		min-height: 800px;
	}
</style>
