<template>
	<div>
		<el-container>
			<el-header>
				<el-menu class="menu" mode="horizontal" :router="true" :default-active="defaultActive">
					<h4>弱电间管理系统</h4>
					<div class="header-right">
						<el-submenu index="#">
							<template slot="title"><i
									class="el-icon-s-custom"></i>{{ realname }}&nbsp;&nbsp;&nbsp;&nbsp;</template>
							<el-menu-item index="#logout" @click="logout">登出</el-menu-item>
						</el-submenu>
					</div>
				</el-menu>
			</el-header>
			<el-main :style="defaultHeight">
				<el-table ref="table" :data="rooms" style="width: 100%" max-height="740" v-loading="loading"
					@filter-change="filterChange">
					<el-table-column prop="campus_name" label="校区" width="110" column-key="campusIds"
						:filters="filters.campusIds" />

					<el-table-column label="楼号及名称" width="120">
						<template slot-scope="scope">
							{{ scope.row.building_number }} {{ scope.row.building_name }}
						</template>
					</el-table-column>

					<el-table-column prop="location" label="具体位置" width="120" />
				</el-table>
			</el-main>
		<el-footer>
			<el-row>
				<el-col :span="8">
					<div>
						<el-button icon="el-icon-menu" circle @click="display"></el-button>
						<div>
							<span style="font-size: 10px;">任务中心</span>
						</div>
					</div>
				</el-col>
				<el-col :span="8">
					<div>
						<el-button icon="el-icon-search" circle @click="searchPosition"></el-button>
						<div>
							<span style="font-size: 10px;">查看地址</span>
						</div>
					</div>
				</el-col>
				<el-col :span="8">
					<div>
						<el-button icon="el-icon-user" circle @click="personalCenter"></el-button>
						<div>
							<span style="font-size: 10px;">个人中心</span>
						</div>
					</div>
				</el-col>
			</el-row>
		</el-footer>
		</el-container>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				defaultHeight: {
					height: ""
				},
				user_id: localStorage.getItem('user_id'),
				realname: localStorage.getItem('realname'),
				loading: true,
				exportVisible: false,
				exportNum: null,
				exportUrl: null,
				search_content: '',
				totalPage: 1,
				rooms: [],
				previewVisible: false,
				previewImages: [],
				filters: {
					campusIds: [],
					typeIds: [],
					hasLock: [{
							text: '有',
							value: true
						},
						{
							text: '无',
							value: false
						}
					],
					hasAc: [{
							text: '有',
							value: true
						},
						{
							text: '无',
							value: false
						}
					],
					hasDevice: [{
							text: '移动设备',
							value: 'has_mobile_device'
						},
						{
							text: '电信设备',
							value: 'has_telecom_device'
						},
						{
							text: '联通设备',
							value: 'has_unicom_device'
						}
					],
					hasEnv: [{
							text: '全部为A级',
							value: 1
						},
						{
							text: '含B级以下',
							value: 2
						},
						{
							text: '含C级以下',
							value: 3
						},
					]
				},
				params: {
					page: 1,
					per_page: 30,
					campus_ids: null,
					type_ids: null,
					has_lock: null,
					has_ac: null,
					has_mobile_device: null,
					has_telecom_device: null,
					has_unicom_device: null,
					has_env: null
				}
			}
		},
		methods: {
			logout() {
				this.$notify({
					title: "登出成功",
					message: `您已登出，请重新登录`,
					type: "success",
					duration: 1000,
					onClose: () => {
						localStorage.clear();
						this.$router.push('/');
					},
				});
			},
			getData() {
				this.loading = true;
				this.API.getRoomList(this.params).then(data => {
					this.totalPage = data['total_page'];
					if (this.params.page > this.totalPage) {
						this.params.page = this.totalPage;
						return;
					}
					this.rooms = data['results'];
					this.loading = false;
				})
			},
			filterChange(value) {
				this.params.page = 1;
				if (value.campusIds !== undefined)
					this.params.campus_ids = value.campusIds.join(',');
				if (value.typeIds !== undefined)
					this.params.type_ids = value.typeIds.join(',');
				if (value.hasLock !== undefined)
					this.params.has_lock = value.hasLock[0];
				if (value.hasAc !== undefined)
					this.params.has_ac = value.hasAc[0];
				if (value.hasDevice !== undefined) {
					this.params.has_mobile_device = null;
					this.params.has_telecom_device = null;
					this.params.has_unicom_device = null;
					value.hasDevice.forEach(e => {
						this.params[e] = true;
					});
				}
				if (value.hasEnv !== undefined)
					this.params.has_env = value.hasEnv[0];

				if (this.params.page === 1)
					this.getData();
				else
					this.params.page = 1;
			},
			clearFilter() {
				this.$refs['table'].clearFilter();
				this.params = {
					page: 1,
					per_page: 30,
					campus_ids: null,
					type_ids: null,
					has_lock: null,
					has_ac: null,
					has_mobile_device: null,
					has_telecom_device: null,
					has_unicom_device: null,
					has_env: null
				};
				this.getData();
			},
			exportData() {
				this.exportVisible = true;
				this.exportUrl = null;
				this.API.exportRooms().then(data => {
					this.exportUrl = this.axios.defaults.baseURL + '/static/' + data['filename'];
					this.exportNum = data['count']
				});
			},
			getHeight() {
				this.defaultHeight.height = window.innerHeight - 120 + "px";
			},
			handleClick(tab, event) {
				console.log(tab, event);
			},
			display() {
				this.$router.push("/display")
			},
			searchPosition() {
				this.$router.push("/searchPosition")
			},
			personalCenter() {
				this.$router.push(`/personalCenter/${localStorage.user_id}`)
			},
		},
		mounted() {
			this.getData();
			this.API.getCampusList().then(data => {
				this.filters.campusIds = [];
				data.forEach(e => {
					this.filters.campusIds.push({
						text: e.name,
						value: e.id
					});
				});
			});
			this.API.getRoomTypes().then(data => {
				this.filters.typeIds = [];
				data.forEach(e => {
					if (e.id !== 1)
						this.filters.typeIds.push({
							text: e.name,
							value: e.id
						});
				});
			});
		},
		watch: {
			'params.page': function() {
				this.getData();
			}
		},
		created() {
			window.addEventListener("resize", this.getHeight());
			this.getHeight();
		}
	}
</script>

<style lang="scss">
	.menu {
		display: flex;
		justify-content: flex-start;
		width: 100%;
	}

	.header-right {
		margin-left: auto;
	}

	.el-footer {
		background-color: #f3f8fa;
	}
</style>
