<template>
	<el-table ref="table" :data="missionData" @selection-change="handleSelectionChange" @filter-change="filterChange"
		@sort-change="sortChange" :default-sort="{prop: 'complete_t', order: 'descending'}" v-loading="loading" stripe
		style="width: 100%" max-height="740">
		<el-table-column type="index" width="30">
		</el-table-column>
		<el-table-column prop="type_name" label="任务类型" width="100" column-key="typeIds" :filters="filters.typeIds" />

		<el-table-column prop="complete_t" label="完成时间" width="150" sortable="custom">
			<template slot-scope="scope">
				{{ scope.row.complete_t ? new Date(scope.row.complete_t * 1000).toLocaleString() : '无' }}
			</template>
		</el-table-column>

		<el-table-column label="状态" width="93" column-key="status" :filters="filters.status" :filter-multiple="false">
			<template slot-scope="scope">
				<el-tag v-if="scope.row.cancelled" type="warning" size="medium">
					已取消</el-tag>

				<el-tag v-else-if="scope.row.deadline_t > scope.row.complete_t" type="success" size="medium">
					按时完成</el-tag>

				<el-tag v-else type="warning" size="medium">
					超时完成</el-tag>
			</template>
		</el-table-column>
	</el-table>
</template>

<script>
	export default {
		methods: {
			deleteMission(row) {
				this.$confirm('此操作将删除任务，是否继续？', '删除任务', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.API.deleteMission(row.id).then(() => {
						this.getData();
						this.$notify({
							title: '成功',
							message: `删除任务(ID: ${row.id})成功`,
							type: 'success',
							duration: 1000
						});
					});
				})
			},
			restartMission(row) {
				this.$confirm('此操作将重启任务，是否继续？', '重启任务', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.API.restartMission(row.id).then(() => {
						this.getData();
						this.$notify({
							title: '成功',
							message: `重启任务(ID: ${row.id})成功`,
							type: 'success',
							duration: 1000
						});
					});
				})
			},
			showRecord(row) {
				this.API.getMissionRecord(row.id).then(data => {
					this.room = data.room;
					this.mission = data.mission;
					this.record = data.record;
					this.recordVisible = true;
				});
			},
			getData() {
				this.loading = true;
				this.API.getMissionList(this.params).then(data => {
					this.missionData = [];
					this.totalPage = data['total_page'];
					if (this.params.page > this.totalPage) {
						this.params.page = this.totalPage;
						return;
					}
					this.missionData = data['results'];
					this.loading = false;
				});
			},
			handleSelectionChange(val) {
				this.multipleSelection = [];
				val.forEach(e => {
					this.multipleSelection.push(e.id);
				});
			},
			deleteMultiMissions() {
				this.$confirm(`此操作将删除 ${this.multipleSelection.length} 个任务，是否继续？`, '删除任务', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.API.deleteMultiMissions(this.multipleSelection).then(() => {
						this.getData();
						this.$refs['table'].clearSelection();
						this.$notify({
							title: '成功',
							message: `删除任务成功`,
							type: 'success',
							duration: 1000
						});
					});
				})
			},
			restartMultiMissions() {
				this.$confirm(`此操作将重启 ${this.multipleSelection.length} 个任务，是否继续？`, '删除任务', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.API.restartMultiMissions(this.multipleSelection).then(() => {
						this.getData();
						this.$refs['table'].clearSelection();
						this.$notify({
							title: '成功',
							message: `重启任务成功`,
							type: 'success',
							duration: 1000
						});
					});
				})
			},
			filterChange(value) {
				this.params.page = 1;
				if (value.campusIds !== undefined)
					this.params.campus_ids = value.campusIds.join(',');
				if (value.typeIds !== undefined)
					this.params.type_ids = value.typeIds.join(',');
				if (value.designaterIds !== undefined)
					this.params.designater_ids = value.designaterIds.join(',');
				if (value.executorIds !== undefined)
					this.params.executor_ids = value.executorIds.join(',');
				if (value.status !== undefined)
					if (value.status.length === 0)
						this.params.status = 'complete';
					else
						this.params.status = value.status[0];

				if (this.params.page === 1)
					this.getData();
				else
					this.params.page = 1;
			},
			sortChange(value) {
				this.params.order_by = value.prop + '_' + value.order;
				if (this.params.page === 1)
					this.getData();
				else
					this.params.page = 1;
			},
			clearFilter() {
				this.$refs['table'].clearFilter();
				this.params = {
					page: 1,
					page_size: 30,
					campus_ids: null,
					type_ids: null,
					designater_ids: null,
					executor_ids: null,
					status: 'complete',
					order_by: 'complete_t_descending'
				};
				this.getData();
			}
		},
		mounted() {
			this.getData();
			this.API.getCampusList().then(data => {
				this.filters.campusIds = [];
				data.forEach(e => {
					this.filters.campusIds.push({
						text: e.name,
						value: e.id
					});
				});
			});
			this.API.getMissionTypes().then(data => {
				this.filters.typeIds = [];
				data.forEach(e => {
					this.filters.typeIds.push({
						text: e.name,
						value: e.id
					});
				});
			});
			this.API.getUserList().then(data => {
				this.filters.userIds = [];
				data.forEach(e => {
					this.filters.userIds.push({
						text: e.realname,
						value: e.id
					});
				});
			})
		},
		watch: {
			'params.page'() {
				this.getData();
			}
		},
		data() {
			return {
				loading: true,
				room: {
					id: null,
					number: null,
					location: null,
					campus: {
						id: null,
						name: null,
					},
					building: {
						id: null,
						number: null,
						name: null
					},
				},
				mission: {
					designater_name: null,
					executor_name: null,
					complete_t: null,
					type_name: ''
				},
				record: {
					clean: null,
					cable: null,
					air: null,
					before_images: [],
					after_images: [],
					description: '',
					problem: ''
				},
				recordVisible: false,
				totalPage: 1,
				missionData: [],
				multipleSelection: [],
				filters: {
					campusIds: [],
					typeIds: [],
					userIds: [],
					status: [{
							text: '按时完成',
							value: 'complete_intime'
						},
						{
							text: '超时完成',
							value: 'complete_late'
						},
						{
							text: '已取消',
							value: 'cancelled'
						}
					]
				},
				params: {
					page: 1,
					page_size: 30,
					campus_ids: null,
					type_ids: null,
					designater_ids: null,
					executor_ids: null,
					status: 'complete',
					order_by: 'complete_t_descending'
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	.full-width {
		width: 100%;
	}
</style>
