<template>
	<div>
		<el-container>
			<el-header>
				<el-menu class="menu" mode="horizontal" :router="true" :default-active="defaultActive">
					<h4>弱电间管理系统</h4>
					<div class="header-right">
						<el-submenu index="#">
							<template slot="title"><i
									class="el-icon-s-custom"></i>{{ realname }}&nbsp;&nbsp;&nbsp;&nbsp;</template>
							<el-menu-item index="#logout" @click="logout">登出</el-menu-item>
						</el-submenu>
					</div>
				</el-menu>
			</el-header>
			<el-main :style="defaultHeight">
				<el-dialog :visible.sync="confirmVisible" width="60%">
					<el-descriptions class="margin-top" title="信息确认" :column="2" border>
						<el-descriptions-item>
							<template slot="label">
								姓名
							</template>
							{{ form.realname }}
						</el-descriptions-item>

						<el-descriptions-item>
							<template slot="label">
								用户名
							</template>
							{{ form.username }}
						</el-descriptions-item>

						<el-descriptions-item>
							<template slot="label">
								新密码
							</template>
							{{ form.new_password != '' ? form.new_password : '未修改' }}
						</el-descriptions-item>
					</el-descriptions>

					<span slot="footer" class="dialog-footer">
						<el-button @click="confirmVisible = false">取 消</el-button>

						<el-button :disabled="disabled" type="primary" @click="submitForm">确 定</el-button>
					</span>
				</el-dialog>

				<el-page-header @back="$router.go(-1)" content="个人中心"></el-page-header>

				<el-divider />

				<div class="detail-main">
					<el-form ref="form" :model="form" :rules="rules" label-width="80px" v-loading="loading">

						<el-form-item label="姓名" prop="realname">
							<el-input class="w250" placeholder="请输入姓名" v-model="form.realname"></el-input>
						</el-form-item>

						<el-form-item label="用户名" prop="username">
							<el-input class="w250" placeholder="请输入用户名" v-model="form.username"></el-input>
						</el-form-item>

						<el-form-item label="新密码" prop="new_password">
							<el-input type="password" class="w250" placeholder="请输入新密码" v-model="form.new_password">
							</el-input>
						</el-form-item>

						<el-form-item>
							<el-button type="primary" @click="onSubmit" :disabled="disabled">提交</el-button>

							<el-button @click="$router.go(-1)">取消</el-button>
						</el-form-item>
					</el-form>
				</div>
			</el-main>
			<el-footer>
				<el-row>
					<el-col :span="8">
						<div>
							<el-button icon="el-icon-menu" circle @click="display"></el-button>
							<div>
								<span style="font-size: 10px;">任务中心</span>
							</div>
						</div>
					</el-col>
					<el-col :span="8">
						<div>
							<el-button icon="el-icon-search" circle @click="searchPosition"></el-button>
							<div>
								<span style="font-size: 10px;">查看地址</span>
							</div>
						</div>
					</el-col>
					<el-col :span="8">
						<div>
							<el-button icon="el-icon-user" circle @click="personalCenter"></el-button>
							<div>
								<span style="font-size: 10px;">个人中心</span>
							</div>
						</div>
					</el-col>
				</el-row>
			</el-footer>
		</el-container>
	</div>
</template>

<script>
	export default {
		data() {
			var newPassValidate = (_, value, callback) => {
				const regex = /^(?=.*\d)(?=.*[a-z])[a-zA-Z0-9~!@#$%^&*]{8,16}$/;
				if (value) {
					if (!regex.test(value))
						callback(new Error('密码必须包含数字和字母, 且长度为8-16位'));
					callback();
				} else {
					callback();
				}
			};
			return {
				defaultHeight: {
					height: ""
				},
				user_id: localStorage.getItem('user_id'),
				realname: localStorage.getItem('realname'),
				loading: true,
				disabled: false,
				confirmVisible: false,
				rules: {
					realname: [{
						required: true,
						message: '请输入姓名',
						trigger: 'blur'
					}],
					username: [{
						required: true,
						message: '请输入用户名',
						trigger: 'blur'
					}],
					new_password: [{
						validator: newPassValidate,
						trigger: 'blur'
					}],
					character: [{
						required: true,
						message: '请选择角色',
						trigger: 'change'
					}]
				},
				charas: {
					0: '超级管理员',
					1: '管理员',
					2: '工作人员'
				},
				campus: [],
				form: {
					realname: '',
					username: '',
					new_password: '',
					character: null,
					campus: []
				}
			}
		},
		methods: {
			onSubmit() {
				this.$refs['form'].validate((valid) => {
					if (valid) {
						this.confirmVisible = true;
					} else {
						return false;
					}
				});
			},
			submitForm() {
				this.disabled = true;
				this.API.updateUser(this.$router.currentRoute.params.id, {
					realname: this.form.realname,
					username: this.form.username,
					password: this.form.new_password,
					character: this.form.character,
					campus: this.form.campus.map(e => e.id),
				}).then(() => {
					this.$notify({
						title: '成功',
						message: '修改成功',
						type: 'success',
						duration: 1000,
						onClose: () => {
							this.disabled = false;
							this.$router.push('/display');
						}
					});
				}).catch(() => {
					this.disabled = false;
					this.confirmVisible = false;
				});
			},
			logout() {
				this.$notify({
					title: "登出成功",
					message: `您已登出，请重新登录`,
					type: "success",
					duration: 1000,
					onClose: () => {
						localStorage.clear();
						this.$router.push('/');
					},
				});
			},
			getHeight() {
				this.defaultHeight.height = window.innerHeight - 120 + "px";
			},
			handleClick(tab, event) {
				console.log(tab, event);
			},
			display() {
				this.$router.push("/display")
			},
			searchPosition() {
				this.$router.push("/searchPosition")
			},
			personalCenter() {
				this.$router.push(`/personalCenter/${localStorage.user_id}`)
			},
		},
		mounted() {
			this.axios.all([
				this.API.getUserDetail(this.$router.currentRoute.params.id),
				this.API.getCampusList()
			]).then(this.axios.spread((user, campus) => {
				campus.forEach(e => {
					if (e.executive_id === null || e.executive_id === parseInt(this.$router
							.currentRoute.params.id))
						this.campus.push(e);
				});
				this.form = user;
				this.loading = false;
			}));
		},
		created() {
			window.addEventListener("resize", this.getHeight());
			this.getHeight();
		}
	}
</script>

<style lang="scss">
	.menu {
		display: flex;
		justify-content: flex-start;
		width: 100%;
	}

	.header-right {
		margin-left: auto;
	}

	.el-footer {
		background-color: #f3f8fa;
	}
</style>
