<template>
	<div>
		<el-table ref="table" :data="missionData" @selection-change="handleSelectionChange"
			@filter-change="filterChange" @sort-change="sortChange"
			:default-sort="{prop: 'deadline_t', order: 'ascending'}" v-loading="loading" style="width: 100%" stripe
			max-height="740">
			<el-table-column width="30">
				<template slot-scope="scope">
					{{scope.$index+1}}
				</template>
			</el-table-column>
			<el-table-column prop="type_name" label="任务类型" width="90" column-key="typeIds" :filters="filters.typeIds" />
			<el-table-column prop="campus_name" label="校区" width="75" column-key="campusIds"
				:filters="filters.campusIds" />
			<el-table-column prop="deadline_t" label="截止时间" width="105" sortable="custom">
				<template slot-scope="scope">
					{{ new Date(scope.row.deadline_t * 1000).toLocaleString() }}
				</template>
			</el-table-column>

			<el-table-column label="操作" width="73">
				<template slot-scope="scope">
					<el-button @click="$router.push(`/submit/${scope.row.id}`)"
						:disabled="new Date().getTime() / 1000 < scope.row.start_t" type="primary" size="small">执行
					</el-button>
				</template>
			</el-table-column>
		</el-table>
	</div>
</template>


<script>
	export default {
		methods: {
			deleteMission(row) {
				this.$confirm('此操作将删除任务，是否继续？', '删除任务', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.API.deleteMission(row.id).then(() => {
						this.getData();
						this.$notify({
							title: '成功',
							message: `删除任务(ID: ${row.id})成功`,
							type: 'success',
							duration: 1000
						});
					});
				})
			},
			cancelMission(row) {
				this.$confirm('此操作将取消任务，是否继续？', '取消任务', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.API.cancelMission(row.id).then(() => {
						this.getData();
						this.$notify({
							title: '成功',
							message: `取消任务(ID: ${row.id})成功`,
							type: 'success',
							duration: 1000
						});
					});
				})
			},
			cancelMultiMissions() {
				this.$confirm(`此操作将取消 ${this.multipleSelection.length} 个任务，是否继续？`, '取消任务', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.API.cancelMultiMissions(this.multipleSelection).then(() => {
						this.getData();
						this.$refs['table'].clearSelection();
						this.$notify({
							title: '成功',
							message: `取消任务成功`,
							type: 'success',
							duration: 1000
						});
					});
				})
			},
			getData() {
				this.loading = true;
				this.API.getMissionList(this.params).then(data => {
					this.missionData = [];
					this.totalPage = data['total_page'];
					if (this.params.page > this.totalPage) {
						this.params.page = this.totalPage;
						return;
					}
					this.missionData = data['results'];
					this.loading = false;
				});
			},
			handleSelectionChange(val) {
				this.multipleSelection = [];
				val.forEach(e => {
					this.multipleSelection.push(e.id);
				});
			},
			deleteMultiMissions() {
				this.$confirm(`此操作将删除 ${this.multipleSelection.length} 个任务，是否继续？`, '删除任务', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.API.deleteMultiMissions(this.multipleSelection).then(() => {
						this.getData();
						this.$refs['table'].clearSelection();
						this.$notify({
							title: '成功',
							message: `删除任务成功`,
							type: 'success',
							duration: 1000
						});
					});
				})
			},
			filterChange(value) {
				this.params.page = 1;
				if (value.campusIds !== undefined)
					this.params.campus_ids = value.campusIds.join(',');
				if (value.typeIds !== undefined)
					this.params.type_ids = value.typeIds.join(',');
				if (value.designaterIds !== undefined)
					this.params.designater_ids = value.designaterIds.join(',');
				if (value.status !== undefined)
					if (value.status.length === 0)
						this.params.status = 'incomplete';
					else
						this.params.status = value.status[0];

				if (this.params.page === 1)
					this.getData();
				else
					this.params.page = 1;
			},
			sortChange(value) {
				this.params.order_by = value.prop + '_' + value.order;
				if (this.params.page === 1)
					this.getData();
				else
					this.params.page = 1;
			},
			clearFilter() {
				this.$refs['table'].clearFilter();
				this.params = {
					page: 1,
					page_size: 30,
					campus_ids: null,
					type_ids: null,
					designater_ids: null,
					status: 'incomplete',
					order_by: 'deadline_t_ascending'
				};
				this.getData();
			}
		},
		mounted() {
			this.getData();
			this.API.getCampusList().then(data => {
				this.filters.campusIds = [];
				data.forEach(e => {
					this.filters.campusIds.push({
						text: e.name,
						value: e.id
					});
				});
			});
			this.API.getMissionTypes().then(data => {
				this.filters.typeIds = [];
				data.forEach(e => {
					this.filters.typeIds.push({
						text: e.name,
						value: e.id
					});
				});
			});
			this.API.getUserList().then(data => {
				this.filters.userIds = [];
				data.forEach(e => {
					this.filters.userIds.push({
						text: e.realname,
						value: e.id
					});
				});
			})
		},
		watch: {
			'params.page'() {
				this.getData();
			}
		},
		data() {
			return {
				loading: true,
				totalPage: 1,
				missionData: [],
				multipleSelection: [],
				filters: {
					campusIds: [],
					typeIds: [],
					userIds: [],
					status: [{
							text: '未完成',
							value: 'incomplete_intime'
						},
						{
							text: '超时未完成',
							value: 'imcomplete_late'
						}
					]
				},
				params: {
					page: 1,
					page_size: 30,
					campus_ids: null,
					type_ids: null,
					designater_ids: null,
					status: 'incomplete',
					order_by: 'deadline_t_ascending'
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	.full-width {
		width: 100%;
	}
</style>
