<template>
	<el-main class="main">
		<el-card>
			<div slot="header">
				<el-page-header @back="$router.go(-1)" content="提交任务" />
			</div>

			<div class="detail-main" v-loading="flags.loading">
				<room-detail v-if="room.building.id" title="弱电间信息" :info="room" />

				<h4 align="left">任务记录</h4>

				<el-form ref="form" :model="form" :rules="rules" label-width="100px">
					<el-form-item label="完成人员" prop="realname">
						<el-input style="width: 250px" readonly v-model="realname"></el-input>
					</el-form-item>

					<el-form-item label="完成时间" prop="complete_t">
						<el-date-picker readonly type="datetime" style="width: 250px" placeholder="选择日期时间"
							v-model="mission.complete_t"></el-date-picker>
					</el-form-item>

					<el-form-item label="任务类型" prop="type_name">
						<el-input style="width: 250px" readonly v-model="mission.type_name"></el-input>
					</el-form-item>

					<el-form-item label="任务说明">
						{{ mission.description }}
					</el-form-item>

					<el-form-item label="工作前照片" prop="before_images">
						<el-upload action="#" :on-preview="handlePreview" :before-remove="beforeRemove"
							:on-remove="(_, fileList) => { form.before_images = fileList; }"
							:http-request="(params) => { uploadImage(form.before_images, params); }"
							:file-list="form.before_images" list-type="picture-card" accept="image/*">
							<el-button size="small" type="primary">点击拍摄</el-button>
							<div slot="tip" align="left">请依次拍摄到达位置照片，全貌照片，细节照片</div>
						</el-upload>
					</el-form-item>

					<el-form-item label="卫生情况" prop="clean">
						<el-radio-group v-model="form.clean">
							<el-radio :label="1">A</el-radio>
							<el-radio :label="2">B</el-radio>
							<el-radio :label="3">C</el-radio>
							<el-radio :label="0">无</el-radio>
						</el-radio-group>
					</el-form-item>

					<el-form-item label="线缆情况" prop="cable">
						<el-radio-group v-model="form.cable">
							<el-radio :label="1">A</el-radio>
							<el-radio :label="2">B</el-radio>
							<el-radio :label="3">C</el-radio>
							<el-radio :label="0">无</el-radio>
						</el-radio-group>
					</el-form-item>

					<el-form-item label="通风情况" prop="air">
						<el-radio-group v-model="form.air">
							<el-radio :label="1">A</el-radio>
							<el-radio :label="2">B</el-radio>
							<el-radio :label="3">C</el-radio>
							<el-radio :label="0">无</el-radio>
						</el-radio-group>
					</el-form-item>

					<el-form-item label="供电情况" prop="power">
						<el-radio-group v-model="form.power">
							<el-radio :label="1">A</el-radio>
							<el-radio :label="2">B</el-radio>
							<el-radio :label="3">C</el-radio>
							<el-radio :label="0">无</el-radio>
						</el-radio-group>
					</el-form-item>
					<el-form-item label="工作后照片" prop="after_images">
						<el-upload action="#" :on-preview="handlePreview" :before-remove="beforeRemove"
							:on-remove="(_, fileList) => { form.after_images = fileList; }"
							:http-request="(params) => { uploadImage(form.after_images, params); }"
							:file-list="form.after_images" list-type="picture-card" accept="image/*">
							<el-button size="small" type="primary">点击拍摄</el-button>
							<div slot="tip" align="left">只需拍摄细节照片</div>
						</el-upload>
					</el-form-item>

					<el-form-item label="完成说明">
						<el-input type="textarea" v-model="form.description"></el-input>
					</el-form-item>
					<el-form-item label="问题说明">
						<el-input type="textarea" v-model="form.problem"></el-input>
					</el-form-item>

					<el-form-item>
						<el-col :span="8">
							<el-button type="primary" @click="() => { flags.presave = false; onSubmit(); }"
								:disabled="flags.disabled">提交</el-button>
						</el-col>
						<el-col :span="8">
							<el-button type="warning" @click="() => { flags.presave = true; onSubmit(); }"
								:disabled="flags.disabled">暂存</el-button>
						</el-col>
						<el-col :span="8">
							<el-button @click="$router.go(-1)" :disabled="flags.disabled">取消</el-button>
						</el-col>
					</el-form-item>
				</el-form>
			</div>
		</el-card>

		<el-dialog :visible.sync="flags.confirmVisible" width="80%">
			<mission-record title="提交确认" :room="room" :mission="mission" :form="form" />

			<span slot="footer" class="dialog-footer">
				<el-button @click="flags.confirmVisible = false">取 消</el-button>

				<el-button :disabled="flags.disabled" type="primary" @click="submitForm">确 定</el-button>
			</span>
		</el-dialog>

		<el-dialog :visible.sync="flags.previewVisible">
			<img width="100%" :src="dialogImageUrl" alt="">
		</el-dialog>
	</el-main>
</template>

<script>
	import RoomDetail from '@/components/RoomDetail.vue';
	import MissionRecord from '@/components/MissionRecord.vue';

	export default {
		components: {
			RoomDetail,
			MissionRecord
		},
		methods: {
			handlePreview(file) {
				this.dialogImageUrl = file.url;
				this.flags.previewVisible = true;
			},
			beforeRemove(file) {
				return this.$confirm(`确定移除 ${ file.name }？`);
			},
			uploadImage(fileList, params) {
				this.API.uploadImage(params.file)
					.then(data => {
						params.onSuccess();
						fileList.push(data);
					})
					.catch(() => {
						params.onError();
					});
			},
			onSubmit() {
				this.$refs['form'].validate((valid) => {
					if (valid) {
						this.flags.confirmVisible = true;
					} else {
						return false;
					}
				});
			},
			submitForm() {
				this.flags.disabled = true;
				this.API.submitMission(this.$router.currentRoute.params.id, this.flags.presave ? 1 : 0, {
					clean: this.form.clean,
					cable: this.form.cable,
					air: this.form.air,
					power: this.form.power,
					description: this.form.description,
					problem: this.form.problem,
					before_images: this.form.before_images.map(item => item.name),
					after_images: this.form.after_images.map(item => item.name)
				}).then(() => {
					this.$notify({
						title: '成功',
						message: '提交成功',
						type: 'success',
						duration: 1000,
						onClose: () => {
							this.flags.disabled = false;
							this.$router.push('/display');
						}
					});
				}).catch(() => {
					this.flags.confirmVisible = false;
					this.flags.disabled = false;
				});
			}
		},
		mounted() {
			this.API.getMissionDetail(this.$router.currentRoute.params.id)
				.then(md => {
					if (md.presave) {
						this.form.clean = md.clean;
						this.form.cable = md.cable;
						this.form.air = md.air;
						this.form.power = md.power;
						this.form.description = md.f_description;
						this.form.problem = md.f_problem;
						this.form.before_images = md.before_images.map(e => ({
							name: e,
							url: this.STATIC_URL + e
						}));
						this.form.after_images = md.after_images.map(e => ({
							name: e,
							url: this.STATIC_URL + e
						}));
					}
					if (md.complete_t !== null)
						this.$notify({
							title: '提示',
							message: '该任务已完成',
							type: 'error',
							duration: 1000,
							onClose: () => {
								this.$router.push('/display');
							}
						});
					else {
						this.mission.designater_name = md.designater_name;
						this.mission.type_name = md.type_name;
						this.mission.description = md.description;
						this.API.getRoomDetail(md.room_id)
							.then(data => {
								Object.keys(this.room).forEach(k => {
									if (data[k] !== undefined) {
										this.room[k] = data[k];
									}
								});
								this.room.campus.id = data.campus_id;
								this.room.campus.name = data.campus_name;
								this.room.building.id = data.building_id;
								this.room.building.number = data.building_number;
								this.room.building.name = data.building_name;
								this.room.type.id = data.type_id;
								this.room.type.name = data.type_name;
								this.flags.loading = false;
							});
					}
				});
		},
		data() {
			var imageBeforeValidation = (rule, value, callback) => {
				if (value.length === 0) {
					callback(new Error('请上传图片'));
				} else {
					callback();
				}
			};
			return {
				realname: localStorage.getItem('realname'),
				flags: {
					loading: true,
					disabled: false,
					previewVisible: false,
					confirmVisible: false,
					presave: false
				},
				dialogImageUrl: '',
				room: {
					id: null,
					number: null,
					campus: {
						id: null,
						name: null,
					},
					building: {
						id: null,
						number: null,
						name: null,
					},
					location: null,
					type: {
						id: null,
						name: null,
					},
					lock: null,
					mobile_device: null,
					telecom_device: null,
					unicom_device: null,
					clean: 0,
					cable: 0,
					air: 0,
					power: 0
				},
				rules: {
					clean: [{
						required: true,
						message: '请选择卫生情况',
						trigger: 'change'
					}],
					cable: [{
						required: true,
						message: '请选择线缆情况',
						trigger: 'change'
					}],
					air: [{
						required: true,
						message: '请选择通风情况',
						trigger: 'change'
					}],
					power: [{
						required: true,
						message: '请选择供电情况',
						trigger: 'change'
					}],
					before_images: [{
						required: true,
						validator: imageBeforeValidation,
						trigger: 'change'
					}]
				},
				mission: {
					designater_name: null,
					executor_name: localStorage.getItem('realname'),
					complete_t: new Date(),
					type_name: null,
					description: null
				},
				form: {
					clean: null,
					cable: null,
					air: null,
					power: null,
					before_images: [],
					after_images: [],
					description: '',
					problem: ''
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	.close-btn {
		position: absolute;
		top: 10px;
		right: 10px;
		z-index: 9999;
	}

	.image-popup {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.5);
		z-index: 9998;
	}

	.pop-image {
		width: 80%;
		height: 80%;
		margin: 10vh 10vw;
	}
</style>
