 <el-card style="width: 80%; margin: auto;">
      <h1>工作前照片/档案照片</h1>
       <div v-if="beforeImages.length > 0" id="beforeImages" class="imageDiv">
        <el-image
          class="img"
          v-for="i in beforeImages"
          :key="i"
          :src="i"
        >
          <div slot="error" class="image-slot">
            <img src="@/assets/loading.gif" alt="" />
          </div>
        </el-image>
      </div>
      <el-empty v-else description="无照片" />
      <el-divider/>

      <h1>工作后照片</h1>
       <div v-if="afterImages.length > 0" id="afterImages" class="imageDiv">
        <el-image
          class="img"
          v-for="i in afterImages"
          :key="i"
          :src="i"
        >
          <div slot="error" class="image-slot">
            <img src="@/assets/loading.gif" alt="" />
          </div>
        </el-image>
      </div>
      <el-empty v-else description="无照片" />
    </el-card>
  </el-main>
</template>

<script>
export default {
  data() {
    return {
      originImages: [],
      beforeImages: [],
      afterImages: [],
    }
  },
  mounted() {
    this.API.getMissionDetail(this.$router.currentRoute.params.id).then(data => {
      data.before_images.forEach(e => {
        this.beforeImages.push(this.STATIC_URL + e);
      });
      data.after_images.forEach(e => {
        this.afterImages.push(this.STATIC_URL + e);
      });
    });
  },
}
</script>

<style>
.image-slot {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: #f5f7fa;
  color: #909399;
  font-size: 14px;
}
.img {
  height: 500px;
  object-fit: contain;
}
.imageDiv {
  text-align: center;
}
</style>
