import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import Display from '@/components/Display'
import Submit from '@/components/Submit'
import MissionRecord from '@/components/MissionRecord'
import RoomDetail from '@/components/RoomDetail'
import LevelTag from '@/components/LevelTag'
import SearchPosition from '@/components/SearchPosition'
import PersonalCenter from '@/components/PersonalCenter'
import MissionHistory from '@/components/MissionHistory'
import MissionCurrent from '@/components/MissionCurrent'
import MissionPresave from '@/components/MissionPresave'
import MissionImages from '@/components/MissionImages'

Vue.use(Router)

export default new Router({
	mode: 'hash',
	routes:[
		{
			path: '/',
			name: 'Main',
			component: Main
		},
		{
			path: '/display',
			name: 'Display',
			component: Display
		},
		{
			path: '/submit/:id',
			name: 'Submit',
			component: Submit
		},
		{
			path: '/missionRecord',
			name: 'MissionRecord',
			component: MissionRecord
		},
		{
			path: '/roomDetail',
			name: 'RoomDetail',
			component: RoomDetail
		},
		{
			path: '/levelTag',
			name: 'LevelTag',
			component: LevelTag
		},
		{
			path: '/personalCenter/:id',
			name: 'PersonalCenter',
			component: PersonalCenter
		},
		{
			path: '/searchPosition',
			name: 'SearchPosition',
			component: SearchPosition
		},
		{
			path: '/missionHistory',
			name: 'MissionHistory',
			component: MissionHistory
		},
		{
			path: '/missionCurrent',
			name: 'MissionCurrent',
			component: MissionCurrent
		},
		{
			path: '/missionPresave',
			name: 'MissionPresave',
			component: MissionPresave
		},
		{
			path: '/missionImages',
			name: 'MissionImages',
			component: MissionImages
		},
	]
}
)