<template>
  <el-main>
    <el-form
      ref="loginForm"
      :model="form"
      :rules="rules"
      label-width="80px"
      class="login-box"
    >
      <h3 class="login-title">弱电间管理系统</h3>
      <el-form-item label="账号"  prop="username">
        <el-input
          type="text"
          placeholder="请输入账号"
          v-model="form.username"
        />
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input
          type="password"
          placeholder="请输入密码"
          v-model="form.password"
        />
      </el-form-item>
      <el-form-item>
        <el-button
          :disabled="disabled"
          type="primary"
          v-on:click="onSubmit()"
          style="width: 80%"
        >登录</el-button>
      </el-form-item>
    </el-form>
  </el-main>
</template>

<script>
export default {
  name: "Login",
  mounted() {
    if (localStorage.getItem("token")) {
      this.$router.push('/display');
    }
  },
  data() {
    return {
      disabled: false,
      form: {
        username: "",
        password: "",
      },
      rules: {
        username: [
          { required: true, message: "请输入账号", trigger: "blur" },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    onSubmit() {
      this.$refs["loginForm"].validate((valid) => {
        if (valid) {
          this.disabled = true;
          this.API.login(this.form.username, this.form.password).then(data => {
          localStorage.user_id = data.user_id;
          localStorage.username = data.username;
          localStorage.realname = data.realname;
          localStorage.character = data.character;
          localStorage.token = data.token;
            this.$notify({
              title: "登录成功",
              message: `欢迎你 ${data.realname}`,
              type: "success",
              duration: 1000,
              onClose: () => {
                this.$router.push("/display");
              },
            });
          }).catch(() => { this.disabled = false; });
        } else {
          return false;
        }
      });
    },
  },
};
</script>

<style scoped>
.login-box {
  -webkit-border-radius: 5px;
  border-radius: 5px;
  margin: 180px auto;
  width: document.documentElement.clientWidth-20px;
  padding: 35px 35px 15px;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 15px #cac6c6;
}
.login-title {
  text-align: center;
  margin: 0 auto 40px auto;
  color: #303133;
}
</style>