<template>
  <el-descriptions class="margin-top" :title="title" :column="4" border>
    <el-descriptions-item :span="4">
      <template slot="label">
        弱电间地址
      </template>
      {{ room.campus.name }} {{ room.building.number }} {{ room.building.name }} {{ room.location }} {{ room.number }}
    </el-descriptions-item>
    
    <el-descriptions-item>
      <template slot="label">
        完成人员
      </template>
      {{ mission.executor_name }}
    </el-descriptions-item>
    
    <el-descriptions-item>
      <template slot="label">
        任务类型
      </template>
      {{ mission.type_name }}
    </el-descriptions-item>

    <el-descriptions-item :span="2">
      <template slot="label">
        完成时间
      </template>
      {{ typeof(mission.complete_t) === 'object' ? 
          mission.complete_t.toLocaleString() : 
          (new Date(mission.complete_t * 1000)).toLocaleString() }}
    </el-descriptions-item>

    <el-descriptions-item :span="4">
      <template slot="label">
        任务说明
      </template>
      {{ mission.description || '无' }}
    </el-descriptions-item>

    <el-descriptions-item>
      <template slot="label">
        卫生情况
      </template>
      <level-tag :data="form.air" />
    </el-descriptions-item>

    <el-descriptions-item>
      <template slot="label">
        线缆情况
      </template>
      <level-tag :data="form.cable" />
    </el-descriptions-item>

    <el-descriptions-item>
      <template slot="label">
        通风情况
      </template>
      <level-tag :data="form.air" />
    </el-descriptions-item>

    <el-descriptions-item>
      <template slot="label">
        电源情况
      </template>
      <level-tag :data="form.power" />
    </el-descriptions-item>

    <el-descriptions-item :span="4">
      <template slot="label">
        工作前图片
      </template>
      <el-image 
        style="width: 100px; height: 100px"
        :src="imageBeforeList.length === 0 ? '' : imageBeforeList[0]" 
        :preview-src-list="imageBeforeList">
        <div slot="error" class="image-slot">
          <span v-if="imageBeforeList.length === 0">无图片</span>
          <span v-else>加载失败</span>
        </div>
      </el-image>
    </el-descriptions-item>

    <el-descriptions-item :span="4">
      <template slot="label">
        工作后图片
      </template>
      <el-image 
        style="width: 100px; height: 100px"
        :src="imageAfterList.length === 0 ? '' : imageAfterList[0]" 
        :preview-src-list="imageAfterList">
        <div slot="error" class="image-slot">
          <span v-if="imageAfterList.length === 0">无图片</span>
          <span v-else>加载失败</span>
        </div>
      </el-image>
    </el-descriptions-item>

    <el-descriptions-item :span="4">
      <template slot="label">
        完成说明
      </template>
      {{ form.description || '无' }}
    </el-descriptions-item>

    <el-descriptions-item :span="4">
      <template slot="label">
        问题说明
      </template>
      {{ form.problem || '无' }}
    </el-descriptions-item>
  </el-descriptions>
</template>

<script>
import LevelTag from '@/components/LevelTag';

export default {
  components: { LevelTag },
  props: [ 'title', 'room', 'mission', 'form' ],
  computed: {
    imageBeforeList() {
      let i = [];
      this.form.before_images.forEach(item => {
        if (typeof(item) == 'object')
          i.push(item.url);
        else if (item.substring(0, 4) != 'http')
          i.push(this.axios.defaults.baseURL + '/static/' + item);
        else
          i.push(item);
      });
      return i;
    },
    imageAfterList() {
      let i = [];
      this.form.after_images.forEach(item => {
        if (typeof(item) == 'object')
          i.push(item.url);
        else if (item.substring(0, 4) != 'http')
          i.push(this.axios.defaults.baseURL + '/static/' + item);
        else
          i.push(item);
      });
      return i;
    }
  }
}
</script>

<style>
.image-slot {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: #f5f7fa;
  color: #909399;
  font-size: 14px;
}
</style>
